import React from "react";
import Box from "./Box.js";

function Sidebar({scrolled}){
  return <div className = {`side ${scrolled ? 'side-scroll': ''}`}>
            <Box text = "Sub-Section-One"/>
            <Box text = "Sub-Section-Two"/>
            <Box text = "Sub-Section-Three"/>
  </div>
}

export default Sidebar;